var $height = '850';
function less745() {
    $('#nav .navbar-static').addClass('navbar-fixed-top');
    $('#nav').css({'margin-bottom' : '54px'});
    $('body').find('nav#primaryNav').addClass('primaryNavResponsive');
    $('body').find('nav#primaryNav #myNavbar').removeClass('responsive-collapse');
    $('body').find('nav#primaryNav #myNavbar').addClass('myNavbar-collapse');
    $('body').find('#shoppingBag').detach().appendTo('header nav#primaryNav')
        .css({'position': 'fixed','left' : '40px', "margin-top": "13px", 'top': '0px',  'width' : '35px', 'height' : '33px',
            'background-image': 'url(/assets/img/new/bascket-collapse2.svg)'}).addClass('pull-left');
    $('body > header nav#primaryNav ol').css({'height' : $height}); //height of menu
    // $('body > header nav#primaryNav ol').css({'max-height' : $maxHeight,'max-height' : $minHeight , 'overflow-y' : 'scroll'}); //height of menu
}

function more745() {
    $('#nav .navbar-static').removeClass('navbar-fixed-top');
    $('#nav').css({'margin-bottom' : '0'});
    $('body').find('nav#primaryNav').removeClass('primaryNavResponsive');
    $('body').find('nav#primaryNav #myNavbar').addClass('responsive-collapse');
    $('body').find('nav#primaryNav #myNavbar').removeClass('myNavbar-collapse');
    $('body').find('#shoppingBag').detach().appendTo('header nav#primaryNav')
        .css({"margin-top": "-103px", 'transition-duration': '0.5s', 'margin-bottom': 'none' , 'top' : '0', 'left' : '0',
            'margin-right' : 'none', 'background-image': 'url(/assets/img/new/basket.svg)' ,'position' : '', 'width': '45px', 'height' : '45px'})
        .addClass('float-left');
    // $('body > header nav#primaryNav ol').css({'height' : '', 'overflow-y' : 'hidden'}); //height of menu
    $('body > header nav#primaryNav ol').css({'height' : ''}); //height of menu
}

