function  scrollMore85() {
    if(! $('#nav .navbar-static #primaryNav').children().hasClass('navbar-brand')){
        $('#nav .navbar-static #primaryNav').prepend('<a style="height: 30px" class="navbar-brand navbar-right hidden-sm hidden-xs" href="/" >'+
            '<img class="menu-logo" src="/assets/img/new/bahooklogo2.svg" alt="bahook.com" style="margin-left: 0 !important;"> </a>');
        $('#nav .navbar-static #primaryNav').find('.navbar-brand').animate({'margin-left': 20 }, 500);

        $('body').find('#shoppingBag').detach().appendTo('header nav#primaryNav')
            .css({"margin-top": "-35px", 'transition-duration': '0.5s', 'margin-bottom': 'none' , 'top' : '0',
                'margin-right' : 'none', 'background-image': 'url(/assets/img/new/basket2.svg)' ,'position' : '', 'width': '45px', 'height' : '45px'})
            .addClass('float-left');
        $('#bhkReviewInMenu').css({'margin-right':'0'});
        $('#bhkReviewInMenuSm').css({'display':'none'});
    }
}

function  scrollLess85() {
    $('#nav .navbar-static #primaryNav').find('.navbar-brand').hide('slow', function () {
        $('#nav .navbar-static #primaryNav').find('.navbar-brand').remove();
    });
    $('body').find('#shoppingBag').detach().appendTo('header nav#primaryNav')
        .css({"margin-top": "-103px", 'transition-duration': '0.5s', 'margin-bottom': 'none' , 'top' : '0',
            'margin-right' : 'none', 'background-image': 'url(/assets/img/new/basket.svg)' ,'position' : '', 'width': '45px', 'height' : '45px'})
        .addClass('float-left');
    $('#bhkReviewInMenu').css({'margin-right':'92px'});
    $('#bhkReviewInMenuSm').css({'display':'block'});
}

if($(window).width() <= '745' ){
    less745();

}else{
    more745();
}

$(window).resize(function() {
    if($(window).width() <= '745'){
        //console.log('3');
        less745();
    }else{
        //console.log('4');
        more745();
    }
});
if($(document).scrollTop() >= 85){
    if($(window).width() <= '745' ){
        //console.log('5');
        less745();

    }else{
        //console.log('6');
        scrollMore85();
    }

}
else{
    if($(window).width() <= '745' ){
        //console.log('7');
        less745();

    }else{
        //console.log('8');
        scrollLess85();
    }

}

$(window).scroll(function () {

    if($(window).width() <= '745'){
        //console.log('9');
        //nothing
    }else{
        //console.log('10');
        if($(document).scrollTop() >= 85){
            //console.log('11');
            $(window).resize(function() {
                if($(window).width() <= '745'){
                    //console.log('12');
                    less745();
                    $('#nav .navbar-static #primaryNav').find('.navbar-brand').remove();
                }
                else{
                    //console.log('13');
                    more745();
                }
            });
            scrollMore85();
        }
        else{
            //console.log('14');
            scrollLess85();
        }
    }

});

function baseMenu() {
    $('.base-menu').show();
    $('#shoppingBag').show();
    $('.search').hide();
    $('#nav .navbar-static').css({'background-color': '#0B3750'});
    }

$('body').on('click', '.header-search',function (e) {
    $('.base-menu').slideUp(100);
    $('#shoppingBag').slideUp(100);
    $('.search').css({'visibility' : 'visible'}).slideDown(100);
    $('#nav .navbar-static').css({'background-color': '#ffffff'});
    $('body').find('nav#primaryNav .search #search').focus();
});

$('body').on('click', '#cancel-search-btn',function (e) {
    e.preventDefault();
    $('.base-menu').slideDown(100);
    $('#shoppingBag').slideDown(100);
    $('.search').slideUp(100);
    $('#nav .navbar-static').css({'background-color': '#0B3750'});
    });


