var container = $("#primaryNav .navbar-collapse ");
// var container2 = $("#primaryNav .navbar-collapse ");
// var container = $(".stepSelector");
var body = $('body'),
    li = $('body > header nav#primaryNav ol li');

$(document).mouseup(function(e) {
    if (container.hasClass('in')){
        e.preventDefault();
        //console.log('55666');
        if (!container.is(e.target) && container.has(e.target).length === 0) {
            //console.log('5555');
            e.preventDefault();
            // if (container.hasClass('in')) {
                //     e.preventDefault();
                //container.removeClass('in');
                $("button.navbar-toggle").click();
                // $("button.navbar-toggle").click();
            // }

            //When closed on the menu, close the submenu
            $(li).find('a#res-menu-angle .res-fa-angle-left').removeClass('fa-angle-down').addClass('fa-angle-left').css({'color':'white'});
            $(li).find('a#res-menu-link').removeClass('yellow').removeClass('white').removeClass('collapsed');
            $(li).find('.res-subMenuItemes').removeClass('in');

        }
    }
});

//when click on subMenu change angle and color of subMenu
// $(li).click(function(){
$(document).ready(function () {
    $('body > header nav#primaryNav ol li #res-menu-angle').click(function () {
        var me = $(this),
            child = me.find('.res-fa-angle-left'),
            // target = $(me).find('#res-menu-link').data('target'),
            // target = $(me).find('#res-menu-angle').data('target'),
            target = $(me).data('target'),
            menu = me.children().eq(1);
        // menu.toggleClass("yellow");
        // console.log(target);


        if (child.hasClass('fa-angle-left')) {
            child.removeClass('fa-angle-left')
                .addClass('fa-angle-down').css({'color': '#febe1b'});
            // $(me).find('#res-menu-link').addClass('yellow').removeClass('white');
            $(me).prev('#res-menu-link').addClass('yellow').removeClass('white');

            $(target).find('ul li a').click(function () {
                $(this).addClass('yellow').removeClass('white');
            });
        }
        else {
            child.removeClass('fa-angle-down')
                .addClass('fa-angle-left').css({'color': 'white'});
            // $(me).find('#res-menu-link').addClass('white').removeClass('yellow');
            $(me).prev('#res-menu-link').addClass('white').removeClass('yellow');
        }
    });
    $('body > header nav#primaryNav ol li #res-menu-link').click(function () {
        $(this).addClass('yellow').removeClass('white');
    });

});
