var promotionShowd = 0;
$(document).ready(function () {
    $(document).on('scroll', function() {
        if ($('.promotions_modal').length && !promotionShowd && $(this).scrollTop()>=400) {
            setTimeout(function () {
                promotionShowd = 1;
                $('.promotions_modal').modal('show');
                $.ajax({
                    url: '/setCookieForPromotion',
                    method: 'GET',
                    success: function (data) {
                        // console.log(data);
                    }
                });
                var display_time = $('.promotions_modal #display_time').val();
                if (display_time !== '0000') {
                    setTimeout(function () {
                        $('.promotions_modal').modal('hide')
                    }, display_time);
                }
            }, 1000);
        }
    });
});
$('.promotions img').click(function(){
    $('.promotions_modal').modal('hide');
});